:root {
  font-style: Nunito;
  --black: #000000bf;
  --white: #fff;
  --red: red;
  --magenta: #f0f;
  --lime: #0f0;
  --grey: gray;
  --yellow: #e4e403;
  --blue1: #2f61b880;
  --cyan: #00e1ff;
  --orange: #fa6502;
  --bgcolor1: #122235;
  --lgreen: #06d85f;
  --yellowlime: #a1f506;
  --darkgrey: #292f3a;
  --darkblue: #111928;
  --blue2: #050520;
  --bgimg1: radial-gradient(at 47% 33%, #000 0, transparent 59%), radial-gradient(at 82% 65%, #111927 0, transparent 55%);
  --pink1: #e97777;
  --pink2: #ff9f9f;
  --pink3: #fcddb0;
}

.dark_mode {
  --bgcolor: var(--bgcolor1);
  --bgimage: var(--bgimg1);
  --clr3: var(--grey);
  --clr4: var(--cyan);
  --clr5: var(--magenta);
  --clr7: var(--yellowlime);
  --clr8: var(--darkgrey);
  --clr9: var(--blue2);
  --clr10: var(--lime);
  --clr11: var(--darkblue);
  --clr12: var(--blue1);
  --clr13: var(--orange);
  --clr14: var(--lime);
  --clr15: var(--red);
  --clr16: var(--white);
  --clr17: var(--yellow);
  --clr18: var(--black);
}

.dark_mode #ovr_l_1 {
  display: block;
}

.dark_mode #ovr_l_2 {
  display: none;
}

.light_mode {
  --bgcolor: var(--pink3);
  --bgimage: var(--pink2);
  --clr3: var(--grey);
  --clr5: var(--magenta);
  --clr6: var(--pink1);
  --clr7: var(--lgreen);
  --clr8: var(--pink1);
  --clr9: var(--blue2);
  --clr10: var(--blue1);
  --clr11: var(--pink2);
  --clr12: var(--blue1);
  --clr13: var(--orange);
  --clr14: var(--lime);
  --clr15: var(--red);
  --clr16: var(--black);
  --clr17: var(--red);
  --clr18: var(--black);
}

.light_mode #ovr_l_1 {
  display: none;
}

.light_mode #ovr_l_2 {
  display: block;
}

.light_mode hr {
  color: #000 !important;
  background-color: #000 !important;
  border-color: #000 !important;
}

.dark_mode hr {
  color: #fff !important;
  background-color: #fff !important;
  border-color: #fff !important;
}

#ovr_msg {
  width: 90%;
}

.light_mode .it_popup #container {
  height: 90%;
  border: solid 3px var(--clr16);
  text-align: left;
  background-image: url("white.f13ec030.png");
  font-size: max(14px, 3vh);
  overflow-y: scroll;
}

html {
  scroll-behavior: smooth;
  color-scheme: dark;
}

body {
  background-color: var(--bgcolor);
  background-image: var(--bgimage);
  color: var(--clr16);
  flex-direction: column;
  font-family: Nunito;
  font-size: max(14px, 3vh);
  display: flex;
}

#overlay {
  width: 100%;
  height: 100%;
  z-index: 10;
  pointer-events: none;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
  color: #fff !important;
}

.ace_gutter {
  z-index: 0;
}

#toasts {
  z-index: 10;
  position: fixed;
  top: 10px;
  right: 10px;
}

#toasts:where(.toast, .column) {
  align-items: center;
  display: flex;
}

#toasts .toast {
  width: 300px;
  background: var(--clr11);
  border: var(--clr16) solid 1px;
  border-radius: 3px;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 15px;
  list-style: none;
  display: flex;
  position: relative;
  overflow: hidden;
}

#toasts .toasttimer {
  height: 1px;
  width: 0%;
  background-color: #ccc;
}

#overlay img {
  width: 90%;
}

details {
  width: 90%;
  align-items: center;
  margin: auto;
}

#report-tag {
