    font-size: max(15px, 4vh);
    position: fixed;
    bottom: 5px;
    left: 50%;
    transform: translateY(50%);
  }

  body:before {
    content: "";
    z-index: -2;
    position: absolute;
    inset: 0;
  }

  #un_edit {
    display: none !important;
  }

  #un_preview {
    min-height: 90%;
    min-width: 95%;
    height: max-content !important;
    color: #000 !important;
    display: block !important;
    overflow: hidden !important;
  }

  #un_preview ol {
    color: #000 !important;
  }

  #output {
    height: max-content;
  }

  .qb_q {
    counter-increment: qno;
  }

  body {
    color: #000;
    background: url("white.f13ec030.png");
    margin: 0;
    font-family: Nunito;
    position: relative;
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
  }

  #ovr_l_1 {
    display: none !important;
  }

  #ovr_l_2 {
    display: block !important;
  }

  .no-print, .no-print * {
    display: none !important;
  }

  hr {
    color: #000 !important;
    background-color: #000 !important;
  }

  #output {
    backface-visibility: visible;
    background: url("white.f13ec030.png");
    border: 3px solid #000;
  }

  #eqb_instr {
    color: #000;
  }

  #eqb_add {
    color: #000 !important;
  }

  .qbtp_q {
    font-size: 14px !important;
  }

  .qb_ttl, .qb_img, .qb_q_ty, .qb_mcq, li {
    break-inside: avoid;
    page-break-after: auto;
    display: block;
    position: relative;
    page-break-inside: avoid !important;
  }

  .qb_mcq {
    flex-direction: row;
    justify-content: space-around;
    display: flex;
  }

  #eqb_add {
    -webkit-touch-callout: none;
    user-select: none;
    display: block;
    position: relative;
  }

  .les_q, .les_expl {
    color: #000 !important;
  }

  .qbtp_q {
    border-bottom: 1px solid #000;
    font-size: max(15px, 4vh);
    position: relative;
    color: #000 !important;
  }

  .qb_q_ty {
    color: var(--clr5) !important;
  }

  footer:before {
    content: "Quarkz!";
  }

  hr, li {
    color: #000;
